body {
  background-color: #b8b8b8 !important; }

.App {
  display: grid;
  place-items: center;
  height: 50vh; }
  .App .title {
    font-size: 72px;
    font-family: "Comic Sans MS", cursive, sans-serif; }
  .App .joinRoom {
    padding: 4px;
    font-size: 16px;
    width: 380px;
    font-family: sans-serif; }
  .App .submit {
    padding: 4px;
    font-size: 16px;
    font-family: sans-serif; }
  .App .btn-container {
    margin-top: 10px; }
    .App .btn-container .btn {
      margin-left: 50px;
      margin-top: 25px;
      background-color: transparent;
      border-radius: 4px;
      color: #181818;
      text-align: center;
      font-size: 11px;
      font-weight: 600;
      text-decoration: none;
      cursor: pointer;
      border: 1px solid #252525;
      height: 38px;
      line-height: 38px;
      padding: 0 30px;
      letter-spacing: .1rem;
      text-transform: uppercase;
      white-space: nowrap;
      box-sizing: border-box; }
      .App .btn-container .btn:hover {
        background-color: #aaa5a5;
        color: #006280; }

.RoomTitle {
    font-size: 30px;
}

p {
    font-size: 15px;
    font-family: sans-serif;
}

ol {
    padding-top: 5px;
    padding-left: 15px;
    font-family: sans-serif;
}

.btn2 {
    margin-left: 50px;
    margin-top: 25px;    
    background-color: transparent;
    border-radius: 4px;
    color: rgb(24, 24, 24);
    text-align: center;
    font-size: 11px;
    font-weight: 600;
    text-decoration: none;
    cursor: pointer;
    border: 1px solid rgb(37, 37, 37);
    height: 38px;
    line-height: 38px;
    padding: 0 30px;
    letter-spacing: .1rem;
    text-transform: uppercase;
    white-space: nowrap;
    box-sizing: border-box;

}
